*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.img-rounded{
  border-radius: 50%;
  width: 300px;
  height: 300px;
  margin-top: 2%;
}

.header-section{
  text-align: center;
  color: aliceblue;
  position: relative;
  width: 100%;
  height: 92vh;
  background-image: linear-gradient(to bottom, rgba(11, 13, 14, 0.918), rgba(2, 20, 29, 0.637));
}

h5{
  animation: textanimation 1s ease-in-out;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}
@keyframes textanimation{
  0%{
      letter-spacing: 20px;
  }

  100%{
      letter-spacing: 1px;
  }
}

.header-icons a{
  margin-left: 5px;
  margin-bottom: 30px;
}

p{
  margin-top: 5px;
}


.nav_bg{
  background-color: #202122;
}

.link-button{
  width: 50px;
  height: 100px;
  margin-left: 100px;

}

.skill{
  background-color: #5d686e;
  text-align: center;
  
}

ul{
  list-style-type: none;
}


.row_poster{
   margin-left: 10px;
   transition: transform 450ms;
}

.row_poster:hover{
  transform: scale(1.04);
}

.form {
  text-align: center;
  width: 100%;
  
}

.btn2{
  margin-bottom: 10px;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  background-color: #3a102f; 
  border: none;
  color: white;
  padding: 10px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}